<template>
  <div class="container py-4">
    <h1 class="mb-4">Klausur</h1>

    <div class="mb-3 d-flex gap-2 align-items-center">
      <label class="form-label mb-0">章を選択:</label>
      <select v-model="selectedChapter" class="form-select w-auto">
        <option value="">すべて表示</option>
        <option v-for="(title, num) in chapterTitles" :key="num" :value="String(num)">
          第{{ num }}章 {{ title }}
        </option>
      </select>
      <button class="btn btn-success" @click="expandAll">Expand All</button>
      <button class="btn btn-secondary" @click="collapseAll">Collapse All</button>
    </div>
    
    <!-- 章ごとに表示 -->
    <div v-for="(chapter, chapterNum) in filteredSections" :key="chapterNum" class="mb-5">
      <h2 class="border-bottom pb-2 mb-3">{{ chapter.title }}</h2>

      <!-- 各節 -->
      <div v-for="section in chapter.sections" :key="section.id" class="mb-4">
        <!-- 本文 -->
        <div class="mb-2">
          <button
            class="btn btn-outline-primary w-100 text-start"
            @click="sectionStates[section.stateIndex].showMain = !sectionStates[section.stateIndex].showMain"
          >
            {{ sectionStates[section.stateIndex].showMain ? '▼' : '▶' }} {{ section.label }}
          </button>
          <div
            v-show="sectionStates[section.stateIndex].showMain"
            class="p-3 border border-primary rounded bg-white"
          >
            <component :is="section.component" />
          </div>
        </div>

        <!-- 解説 -->
        <div>
          <button
            class="btn btn-outline-info w-100 text-start"
            @click="sectionStates[section.stateIndex].showEx = !sectionStates[section.stateIndex].showEx"
          >
            {{ sectionStates[section.stateIndex].showEx ? '▼' : '▶' }} {{ section.label }} - Explanation
          </button>
          <div
            v-show="sectionStates[section.stateIndex].showEx"
            class="p-3 border border-info rounded"
            style="background-color: #f0faff;"
          >
            <component :is="section.exComponent" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref, computed, defineAsyncComponent, onMounted } from 'vue';


onMounted(() => {
  window.scrollTo({ top: 0, behavior: 'auto' });
});

const selectedChapter = ref('');

// 章タイトル一覧
const chapterTitles = {
  2: '画面への表示とキーボードからの入力',
  3: 'プログラムの流れの分岐',
};

// 元データ：[節ラベル, コード]
const rawSections = [
  ['2-1 画面への表示', '21'],
  ['2-2 メッセージの出力', '22'],
  ['3-1 if文の使い方', '31'],
  ['3-2 elseの使い方', '32'],
];

// 節を章ごとにグループ化
const groupedSections = {};
const sectionStates = ref([]);

const filteredSections = computed(() => {
  if (!selectedChapter.value) {
    return groupedSections;
  }

  // string から number に変換して比較
  const selected = parseInt(selectedChapter.value, 10);
  return {
    [selected]: groupedSections[selected],
  };
});

rawSections.forEach(([sectionLabel, code]) => {
  const id = `chapter01_0${code}`;
  const chapterNum = parseInt(code.slice(0, 1), 10); // ← '15021' → '02' → 2章
  const chapterTitle = chapterTitles[chapterNum] || '未定義の章';

  if (!groupedSections[chapterNum]) {
    groupedSections[chapterNum] = {
      title: `第${chapterNum}章 ${chapterTitle}`,
      sections: [],
    };
  }

  groupedSections[chapterNum].sections.push({
    id,
    label: sectionLabel,
    component: defineAsyncComponent(() => import(`./chapter01/${id}.vue`)),
    exComponent: defineAsyncComponent(() => import(`./chapter01/${id}_ex.vue`)),
    stateIndex: sectionStates.value.length,
  });

  sectionStates.value.push({ showMain: false, showEx: false });
});

// 展開・折りたたみ操作
function expandAll() {
  sectionStates.value.forEach(s => { s.showMain = true; s.showEx = true; });
}
function collapseAll() {
  sectionStates.value.forEach(s => { s.showMain = false; s.showEx = false; });
}
</script>
